.container {
  width: 100%;
  padding-left: 15px;
  padding-right: 15px;
  margin: 0 auto;
  /* outline: 2px solid red; */

  @include media(414px) {
    width: 414px;
  }

  @include media(1024px) {
    width: 1024px;
    padding-left: 47px;
    padding-right: 47px;
  }

  @include media(1280px) {
    width: 1280px;
    padding-left: 85px;
    padding-right: 85px;
  }
}